
:root { 
    --base_bg_color: rgb(230, 230, 230);
    --base_bg_overlay_color: rgba(220, 220, 220, 0.5);
    --base_fg_color: rgb(0, 0, 0);
    --base_accent_color: rgb(70, 181, 232);
    --navigation_bg_color: rgba(233, 233, 233, 0.8);
    --hover_transition_speed: .5s;
    --toc_default_color: rgba(200, 200, 200, 0.05);
}

[data-theme="dark"] {
    --base_bg_color: rgb(39, 39, 41);
    --base_bg_overlay_color: rgba(30, 30, 30, 0.5);
    --base_fg_color: white;
    --base_accent_color: rgb(232, 150, 70);
    --navigation_bg_color: rgba(40, 40, 40, 0.8);
    --hover_transition_speed: .5s;
    --toc_default_color: rgba(235, 235, 235, 0.05);
}

.top-banner {
    position:absolute; 
    top: 0px;
    width:100%; 
    object-fit:cover;
    z-index: -100; 
    justify-content: center;
    object-position: center center;
    mask-image: linear-gradient(rgba(0,0,0,0.8) 20%, rgba(0,0,0,0.8) 60%, rgba(0,212,255,0) 100%);
    -webkit-mask-image: linear-gradient(rgba(0,0,0,0.8) 20%, rgba(0,0,0,0.8) 60%, rgba(0,212,255,0) 100%);
}

.page-content {
    padding-top: 3vh;
    padding-bottom: 3vh;
}

.word-wrap-dots {
    -ms-word-break: break-all;
    word-break: break-all;
    word-break: break-word;
}

#search-container {
    display: flex;
    border-radius: 1rem;
    border: 1px solid var(--base_bg_overlay_color);
    background: var(--base_bg_overlay_color);
    padding: 0.5rem;
}

#search-input {
    background: center;
    border: 0;
    border-radius: 0;
    padding: 0.18rem 0.3rem;
    color: var(--base_fg_color);
    font-size: 95%;
}

.search-results {
    position:absolute;
    width: 95%;
    max-height: 80vh;
    background-color: var(--base_bg_color);
    border-color: var(--base_bg_overlay_color);
    border-width: 3px;
    border-style: solid;

    a {
        color: var(--base_fg_color);
    }
    
    li {
        list-style: none;
        margin: 1rem;
    }
}

.page-link {
    background-color: rgb(50,50,50);
    border-color:rgb(50,50,50);
}

body {
    background-image:url(/images/main_bg.png);
    background-position: center top; 
    background-size: 100% auto;
    background-color:var(--base_bg_color);

    margin: 0;
    padding: 0;
    width: 100%;
    font-family: 'Montserrat', sans-serif;
    font-weight: 300;
    color: var(--base_fg_color);
    scroll-margin-top: 1.5em;
}

.page-gradient {
    position: fixed;
    z-index: -1000;
    top: 0px;
    width: 100%;
    height: 10vmax;
    background-image: linear-gradient(rgba(0, 0, 0, 0.3) 50%, rgba(0, 0, 0, 0));
}

// Print-friendly!
@media print { 
    body {
        background-color: white;
        color: black;
    }

    @for $i from 1 through 12 {
        .col-print-#{$i} {
            width: #{percentage(calc(round($i*8.33) / 100))} !important;
            float: left;
            flex: #{percentage(calc(round($i*8.33) / 100))} !important;
            max-width: #{percentage(calc(round($i*8.33) / 100))} !important;
        }
    }
}

hr {
    border-top: 1px solid rgba(255, 255, 255, 0.2);
}

#navbar {
    transition: 0.3s ease;
}

.footer {
    position: fixed;
    bottom: 0;
    width: 100%;
    display: flex;
    padding-left: 20px;
    padding-right: 20px;
    z-index: 1000;
}

.navbar-brand {
    font-size: 30px;
}

.navbar
{

    .navbar-nav {
        flex-flow: row;

    }

    .nav-link {
        text-transform: uppercase;
        font-size: 18px;
        transition: border 1.0s ease;
    }

    .nav-item {
        &.active, &:hover {
            font-weight: bold;
        }
    }

    & ul {
        display: flex !important;
        justify-content: center !important;
        font-size: 20px;

        li {
            padding-left: 5px;
            padding-right: 5px;
        }
    }
}

.navbar-brand small {
    padding-left: 20px;
    display: block;
    font-size: 12px;
    margin-top: -10px;
}

/*Non-highlighted link*/
.blacklink {
    color: var(--base_fg_color);

    &:visited {
        color: var(--base_fg_color);
    }

    &:hover {
        color: var(--base_fg_color);
        text-decoration: none;
    }

    &:active {
        color: var(--base_fg_color);
    }
}
/*End non-highlighted link*/

/*project page*/
.project-header {
    text-transform: uppercase;
    letter-spacing: 3px;
    font-weight: bold;
    font-size: 40px;
    margin-top: 30px;
}
/*wrapping container for project page*/

.project-container {
    & .row {
        padding-top: 10px;
        padding-bottom: 10px;
    }
}

.project-info {
    background-color: var(--base_bg_overlay_color);
    padding: 10px;
}

.project-video {
    box-shadow: 5px 10px 20px 15px var(--base_bg_overlay_color);
    margin-bottom: 20px;
}

.project-images {
    padding-top: 10px;

    & img {
        padding-top: 5px;
        padding-bottom: 5px;
        width: 100%;
        height: auto;
        border-radius: 10px;

        &:hover {
            -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
            -webkit-transform: translateZ(0) scale(1.05);
            /* Safari and Chrome */
            -moz-transform: scale(1.05);
            /* Firefox */
            -ms-transform: scale(1.05);
            /* IE 9 */
            -o-transform: translateZ(0) scale(1.05);
            /* Opera */
            transform: translateZ(0) scale(1.05);
        }
    }
}
/*project page end*/

/*404 page*/
.notfound {
    background-color: rgb(240, 240, 240);
}
/*404 page end*/

.aboutme-background {
    z-index: -1;
    background-image: linear-gradient(var(--base_bg_color) 10%, rgba(0,0,0,0.0) 80%, var(--base_bg_color)), url('../images/background.jpg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    position: absolute;
    height: 40em;
    width: 100%;
    top: 0px;
}

.aboutme-description {
    font-size: 18px;
}

.aboutme-section {
    padding-top: 10px;
    padding-bottom: 10px;
    margin-top: 20px;
    margin-bottom: 20px;
    background-color: var(--base_bg_overlay_color);
}

.list-group-item {
    background-color: inherit;
}

.drop-shadow {
    -webkit-box-shadow: 5px 5px 10px -2px rgba(0,0,0,0.5); 
    box-shadow: 5px 5px 10px -2px rgba(0,0,0,0.5);   
}

/*project cover*/
/*Overview Pages*/

.professional-project {
    padding-top: 1rem;
    padding-left: 0.2rem!important;
    padding-right: 0.2rem!important;
}

.project-cover {
    position: relative;
    opacity: 0.75;
    transition: opacity var(--hover_transition_speed) ease;

    -webkit-box-shadow: 5px 5px 10px -2px rgba(0,0,0,0.5); 
    box-shadow: 5px 5px 10px -2px rgba(0,0,0,0.5);

    &:hover {
        opacity: 1.0;
    }

    & .project-cover-info {
        padding: 0.7em;
        position: absolute;
        left: 0px;
        bottom: 0px;
        width: 100%;
        pointer-events: none;
        color: white;
        background-image: linear-gradient(rgba(0, 0, 0, 0), rgba(33, 37, 43, 0.5), rgb(33, 37, 43));

        .info {
            position: absolute;
            padding: 0.7em;
            bottom: 0px;
            right: 0px;
            text-align: right;
        }

        img {
            padding: 5px;
        }

        h3 {
            margin-bottom: 0.2rem;
        }
        h5 {
            margin-bottom: 0.2rem;
        }
        h6 {
            margin-bottom: 0.2rem;
        }
    }
}

@media only screen and (max-width: 768px) {
    .project-cover {
        h3 {
            font-size: 14px;
        }
        h5 {
            font-size: 12px;
        }
        h6 {
            display: 10px;
        }
    }
}

@media only screen and (max-width: 400px) {
    .project-cover {
        h3 {
            font-size: 12px;
        }
        h5 {
            font-size: 10px;
        }
        h6 {
            font-size: 9px;
        }
    }
}
/*project cover end*/

/*Video overlay*/
.video-overlay {
    position: relative;
    height: auto;

    & img {
        width: 100%;
    }

    &:hover video {
        opacity: 1;
    }

    & video {
        position: absolute;
        left: 0px;
        top: 0px;
        width: 100%;
        height: 100%;
        opacity: 0;
        transition: var(--hover_transition_speed) ease;
    }
}
/*Video overlay end*/

/*Image swap on hover*/
.imgswap {
    position: relative;
    & img:last-child {
        position: absolute;
        left: 0px;
        top: 0px;
        transition: var(--hover_transition_speed) ease-in;
        opacity: 0;
    }
    &:hover {
        & img:first-child {
            transition: var(--hover_transition_speed) ease-in;
            opacity: 0;
        }
        & img:last-child {
            transition: var(--hover_transition_speed) ease-in;
            opacity: 1;
        }
    }
}
/*Image swap on hover end*/

/*Overview Pages End*/
.skills-container {
    & .company-icon {
        width: 70px;
        height: 70px;
        border-radius: 10px;
    }
    & p {
        margin-top: 5px;
        margin-bottom: 5px;
    }
}

/* footer */
@media only screen and (max-width: 768px) {
    .footer * {
        font-size: 60%;
    }
}
/* footer end */

/**OTHER**/
.btn {
    border: 0 none;
    letter-spacing: 1px;
    text-transform: uppercase;
    color: var(--base_fg_color);
    background-color: var(--base_bg_color);

    &.sharp {
        border-radius: 0;
    }
    &:hover {
        color: white;
    }
}

.btn:focus,
.btn:active:focus,
.btn.active:focus {
    outline: 0 none;
}
/**OTHER END**/

/** TABLE **/
table {
    margin-bottom: 2em;
    width: 100%;
  }
th {
    border-bottom: 2px solid #ccc;
    font-weight: bold;
    text-align: left;
}
td { border-bottom: 1px solid #ddd; }
    caption, th, td {
    padding: 4px 10px 4px 0;
}
caption {
    background: #f1f1f1;
    padding: 10px 0;
    margin-bottom: 1em;
}

tr,td,th {
    vertical-align: middle;
}

/* Use this if you use span-x classes on th/td. */
table .last { 
    padding-right: 0; 
}

.table-of-contents {

    ul {
        padding-left: 1em;
        font-weight: 200;
        margin: 0;
        border: 0;
        vertical-align: baseline;
        list-style: none;
    }

    a {
        display: block;
        padding-left: 20px;
        list-style-type: none;
        border-left:0.2em solid var(--toc_default_color);
        padding: 0.2em 0px 0.2em 0.9em;
        margin: 0px 0.2em;
        min-width: 100%;
        transition: color 0.3s ease 0s, border-left 0.3s ease 0s;
        text-decoration: none;
        font-size: 0.95rem;

        &.current {
            border-left:0.75em solid var(--base_accent_color);
        }

        &:hover {
            border-left:0.75em solid var(--base_accent_color);
        }

        &:visited {
            color: var(--base_fg_color);
        }
        &:link {
            color: var(--base_fg_color);
        }
    }
}

.blog-index-entry {
    padding-left: 20px;
    list-style-type: none;
    border-left:0.2em solid var(--toc_default_color);
    padding: 0.2em 0px 0.2em 0.9em;
    margin: 0px 0.2em;
    min-width: 100%;
    transition: color 0.3s ease 0s, border-left 0.3s ease 0s;
    text-decoration: none;

    &.current {
        border-left:0.75em solid var(--base_accent_color);
    }

    &:hover {
        border-left:0.75em solid var(--base_accent_color);
    }

    &:visited {
        color: var(--base_fg_color);
    }
    &:link {
        color: var(--base_fg_color);
    }
}

.social-buttons {
    padding-left: 0;
    list-style: none;

    & li {
        display: inline-block;
    }

    & svg {
        width: 1.5em;
        height: 1.5em;
        margin: 0.2em;
        transition: fill 0.3s ease 0s, stroke 0.3s ease 0s;
        stroke: rgba(0, 0, 0, 0);
        fill: var(--base_fg_color);
        
        &:hover {
            fill: rgba(0, 0, 0, 0);
            stroke: var(--base_fg_color);
        }
    }
}

.blog-post {
    background-color: var(--base_bg_overlay_color); 
    padding:20px; 
    border-radius:10px;
}

.blog-sidebar {
    & h3 {
        padding-bottom: 0.2em;
        border-bottom: 1px solid;
    }
}

$theme_switcher_color_dark: rgb(0, 0, 0);
$theme_switcher_color_light: rgb(255, 255, 255);

.theme-switcher
{
    position: relative;
    width: 60px;
    height: 30px;
    margin: 0 auto;
    border-radius: 40px;
    
    span {
        pointer-events: none;
        position: absolute;
        top: 0px;
        right: 0px;
        bottom: 0px;
        left: 0px;
        overflow: hidden;
        opacity: 1;
        background-color: $theme_switcher_color_light;
        border-radius: 40px;
        transition: 0.2s ease background-color, 0.2s ease opacity;

        &:before, &:after {
            content: '';
            position: absolute;
            top: 0px;
            width: 25px;
            height: 25px;
            border-radius: 50%;
            transition: 0.5s ease transform, 0.2s ease background-color;
        }

        &:before { 
            background-color: $theme_switcher_color_light;
            transform: translate(-40px,-6px);
            z-index: 1;
        }
        &:after {
            background-color: $theme_switcher_color_dark;
            transform: translate(3px,2px);
            z-index: 0;
        }
    }

    input[type="checkbox"] {
        width: 100%;
        height: 100%;

        &:checked {
            + span {
                background-color: $theme_switcher_color_dark;
                &:before {
                    background-color: $theme_switcher_color_dark;
                    transform: translate(20px,-5px);
                }
                &:after {
                    background-color: $theme_switcher_color_light;
                    transform: translate(30px,2px);
                }
            }
        }
    }
}